<template>
    <div class="quiz-res-msg">{{  caption  }}</div>
    <TransitionGroup name="graphic">
        <svg v-if="show" viewBox="0 0 1000 750" id="diagram" :width="width" :height="height">
            <defs>
                <filter id="circleShadow">
                    <feDropShadow flood-color="lightgray" />
                </filter>
            </defs>
            <circle :cx="width / 2" :cy="height / 2" :r="5 / 6 * d" filter="url(#circleShadow)" fill="white"
                stroke="transparent" />
            <line v-for="(t, i) in personTypes" :key="i" :x1="width / 2" :y1="height / 2" :x2="getX2(i)" :y2="getY2(i)"
                stroke="black" />
            <circle v-for="n in numberOfCircles" :key="n" :cx="width / 2" :cy="height / 2" :r="circleRadius(n)"
                fill="transparent" stroke="lightgray" />
            <polygon id="myPoly" :points="ratiosAsPolygon" fill="var(--yellow-fill)" stroke="var(--yellow)" />

            <image v-for="(t, i) in personTypes" :key="i" :xlink:href="getImgSrc(t.filename)" :x="getImgLeft(i)"
                :y="getImgTop(i)" />
        </svg>
    </TransitionGroup>
</template>

<script>

export default {
    props: {
        answers: Array,
        testName: String
    },
    name: 'RadarDiagram',
    created() {
        import(`@/assets/quizzes/${ this.testName }/mask.json`).then(p => {
                this.mask = p.mask;
                this.personTypes = p.types;
                this.show = true;
            })
    },
    data() {
        return {
            caption: 'Testen er slut. Her kan du se, hvilken persontype der passer bedst på dig.',
            mask: [],
            personTypes: [],
            d: 320,
            height: 750,
            width: 1000,
            numberOfCircles: null,
            show: false
        };
    },
    computed: {
        ratiosAsPolygon() {
            // Applying the mask to find the answer distribution.
            const dist = this.answers
                .map((e, i) => this.mask[i][e]);
            const ratios = this.personTypes
                .map((e, i) => dist
                    .filter(d => i === d).length / dist.length);

            // Find the highest proportion        
            const maxRatio = Math.max(...ratios);
            const numberOfGridLines = 10;
            const numberOfCircles = Math.floor(maxRatio * (100 / numberOfGridLines)) + 1

            // Update data variable:
            this.setNumberOfCircles(numberOfCircles);

            // The ratio matching the radius of the outer circle.
            const outerBoundRatio = numberOfCircles / 10;

            return ratios
                .map((ratio, index) => {
                    const r = this.d / 2;
                    const cx = this.width / 2;
                    const cy = this.height / 2;
                    const angle = this.calculateAngle(index);
                    const xOffset = cx + r / outerBoundRatio * ratio * Math.cos(angle);
                    const yOffset = cy + r / outerBoundRatio * ratio * Math.sin(angle);
                    return `${ xOffset },${ yOffset }`;
                })
                .join(' '); // See pen!
        }
    },
    methods: {
        setNumberOfCircles(n) {
            this.numberOfCircles = n;
        },
        circleRadius(n) {
            return n * this.d / 2 / this.numberOfCircles;
        },
        calculateAngle(index) {
            const angularIncrement = 2 * Math.PI / this.personTypes.length;
            return index * angularIncrement - Math.PI / 2;
        },
        getImgLeft(index) {
            const imgWidth = 270;
            const angle = this.calculateAngle(index);
            return this.width / 2 + (19 * this.d / 20) * Math.cos(angle) - imgWidth / 2;
        },
        getImgTop(index) {
            const imgHeight = 200;
            const angle = this.calculateAngle(index);
            return this.height / 2 + (5 * this.d / 6) * Math.sin(angle) - imgHeight / 2;
        },
        getImgSrc(filename) {
            return require(`@/assets/quizzes/${ this.testName }/type_imgs/${ filename }`);
        },
        getTextTop() {
            return 0;
        },
        getTextLeft() {
            return 0;
        },
        getX2(index) {
            const angle = this.calculateAngle(index);
            const l = this.d / 2;
            return this.width / 2 + l * Math.cos(angle);
        },
        getY2(index) {
            const angle = this.calculateAngle(index);
            const l = this.d / 2;
            return this.height / 2 + l * Math.sin(angle);
        }
    }
}
</script>

<style scoped>
#diagram {
    margin: auto;
    max-width: 100%;
}

#myPoly {
    stroke-width: 0.25rem;
}

.quiz-res-msg {
    width: 16rem;
    background-color: var(--dark-blue);
    color: white;
    margin-bottom: 1.0rem;
    padding: 1rem;
    position: absolute;
    left: 1rem;
    top: 2rem;
    z-index: 100;
}

.graphic-enter-active,
.graphic-leave-active {
    transition: all 0.6s ease-in-out;
}

.graphic-enter-from,
.graphic-leave-to {
    transform: translateX(-50%) rotate(-180deg);    
    opacity: 0;
}
</style>