<template>
    <div v-if="!showResult">
        <button @click="testStarted = true" v-if="!testStarted" type="button">
            {{  btnTxt  }}
        </button>
        <div v-if="testStarted" id="testWrapper">
            <div class="question">{{  currentIndex + 1  }}.&nbsp; {{  questions[currentIndex].question  }}</div>
            <TransitionGroup name="fade">
                <div v-if="showOptions">
                    <OptionBox v-for="(o, optionIndex) in questions[currentIndex].options" :key="optionIndex"
                        :optionIndex="optionIndex" :optionText="o" v-model="answers[currentIndex]"
                        :groupName="'options' + currentIndex" />
                </div>
            </TransitionGroup>
            <div class="navigation-btns" v-if="showOptions">
                <!-- Do not show previous btn on first question -->
                <button @click="currentIndex--" v-if="currentIndex > 0" class="float-start" type="button">
                    Forrige
                </button>

                <!-- Only enable next btn if current question is answered -->
                <button @click="next()" :disabled="!isAnswered" v-if="!isLast" class="float-end" type="button">
                    Næste
                </button>

                <!-- Only show on final question and enable if answered -->
                <button @click="endQuiz()" :disabled="!isAnswered" v-if="isLast" class="float-end" type="button">
                    Afslut
                </button>
            </div>
        </div>
    </div>

    <div v-if="showResult">
        <RadarDiagram :answers="answers" v-if="type === 'test'" :testName="$route.params.uri" />
        <ReportPage :answers="answers" v-if="type === 'quiz'" :testName="$route.params.uri" />
    </div>
</template>

<script>
// Components:
import OptionBox from '@/components/OptionBox.vue'
import RadarDiagram from '@/components/RadarDiagram.vue'
import ReportPage from '@/components/ReportPage.vue'

export default {
    components: {
        OptionBox,
        RadarDiagram,
        ReportPage
    },
    data() {
        return {
            currentIndex: 0,
            testStarted: false,
            showResult: false,
            questions: [],
            answers: [],
            PIN: '',
            type: '',
            showOptions: true
        }
    },
    created() {
        // watch the params of the route to fetch the data again
        this.$watch(
            () => this.$route.params,
            () => this.getQuiz(),
            { immediate: true }
        )
    },
    computed: {
        isFirst() {
            return this.currentIndex === 0;
        },
        isLast() {
            return this.currentIndex === this.questions.length - 1;
        },
        isAnswered() {
            return this.answers[this.currentIndex] !== undefined;
        },
        btnTxt() {
            const d = this.type === 'quiz' ? 'quizzen' :
                this.type === 'test' ? 'testen' : ''
            return `Begynd ${ d }!`
        }
    },
    methods: {
        getQuiz: function () {
            const quizData = () => import(`@/assets/quizzes/${ this.$route.params.uri }/quiz.json`);
            quizData().then((p) => {
                this.questions = p.questions;
                this.PIN = p.PIN;
                this.type = p.type;
            })
        },
        endQuiz() {
            let guess = prompt("Indtast PIN, for at se dit resultat!");
            const timeout = 1000; // 1 sec. cool down.
            const pin = this.PIN; // data vars not available within nested func. scope.

            function reprompt() {
                guess = prompt("Den indtastede PIN er forkert. Prøv igen!");
                return checkPin();
            }

            function checkPin() {
                if (guess === null) {
                    return false;
                } else if (guess !== pin) {
                    window.setTimeout(() => {
                        return reprompt();
                    }, timeout);
                } else {
                    return true;
                }
            }

            this.showResult = checkPin();
        },
        next() {
            this.showOptions = false;
            setTimeout(() => {
                this.currentIndex++;
                this.showOptions = true;
            }, 200)

        }
    }
}
</script>
<style>
.fade-enter-active,
.fade-leave-active {
    transition: all 0.2s ease-in-out;
}

.fade-enter-from,
.fade-leave-to {
    /* transform: translateX(50%); */
    opacity: 0;
}
</style>