<template>
    <div class="option" :class="{ active: optionIndex === modelValue }">
        <div class="radio-box">
            <input class="radio" type="radio" @change="e => $emit('update:modelValue', Number(e.target.value))"
                :value="optionIndex" :id="'option' + optionIndex" :checked="optionIndex === modelValue" />
        </div>
        <label :for="'option' + optionIndex" class="option-text" v-html="optionText">
        </label>
    </div>
</template>

<script>
export default {
    props: {
        optionIndex: Number,
        optionText: String,
        modelValue: Number
    },
    emits: [
        'update:modelValue'
    ]
}
</script>