<template>
    <svg viewBox="0 0 1000 750" id="diagram" :width="width" :height="height">
        <defs>
            <filter id="circleShadow">
                <feDropShadow flood-color="lightgray" />
            </filter>
        </defs>
        <TransitionGroup name="circlestats">
            <g id="circleStats" v-if="show">
                <circle :cx="width / 2 + 75" :cy="height / 2 - 60" :r="5 / 6 * d" filter="url(#circleShadow)" fill="white"
                stroke="transparent" />
                <text>
                    <tspan x="500" y="180" class="result-text">Du fik</tspan>
                    <tspan x="490" y="400" fill="#FD4197" class="big-number">{{ numberOfCorrectAnswers }}</tspan>
                    <tspan x="460" y="480" class="result-text">{{ correct }} svar</tspan>
                </text>
            </g>
        </TransitionGroup>
        <image :xlink:href="successImg" x="0" y="80" id="successImg" />
    </svg>
</template>

<script>

export default {
    props: {
        answers: Array,
        testName: String
    },
    name: 'ReportPage',
    created() {
        import(`@/assets/quizzes/${ this.testName }/mask.json`).then(p => {
                this.mask = p.mask;
                this.show = true;
            })
    },
    data() {
        return {
            mask: [],
            height: 750,
            width: 1000,
            d: 320,
            numberOfCircles: null,
            show:false
        };
    },
    computed: {
        numberOfCorrectAnswers() {
            return this.answers.reduce((p, c, i) => this.mask[i] === c ? ++p : p, 0);
        },
        correct() {
            return this.numberOfCorrectAnswers === 1 ? 'rigtig' : 'rigtige';
        },
        successImg() {
            return require(`@/assets/quizzes/${this.testName}/success.png`)
        }
    },
    methods: {
    }
}
</script>

<style scoped>
#diagram {
    margin: auto;
    max-width: 100%;
}

#myPoly {
    stroke-width: 0.25rem;
}

.quiz-res-msg {
    width: 16rem;
    background-color: var(--dark-blue);
    color: white;
    margin-bottom: 1.0rem;
    padding: 1rem;
    position: absolute;
    left: 1rem;
    top: 2rem;
}

.big-number {
    font-weight: bolder;
    font-size: 192pt;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.result-text {
    font-size: 36pt;
}

#successImg {
    transform: rotate(7deg);
}


.circlestats-enter-active,
.circlestats-leave-active {
    transition: all 0.6s ease-in-out;
}

.circlestats-enter-from,
.circlestats-leave-to {
    transform: translateX(33%);    
    opacity: 0;
}

</style>